.habilidades-container {
    height: 100vh;
    flex-direction: column;
    display: flex;
    align-content: center;
    align-items: center;
    background-color: #71677c;
    justify-content: space-around;
    padding: 15px;
    }

.barrinha-container {
    border: 2px solid white;
    border-radius: 30px;
    flex: 1;
    display: flex;
    flex-direction: row;
    margin-left: 15px;
}

.habilidade-container {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.preenchimento-barrinha {
    background-color: white;
    border-radius: 30px;
}