.sobre-mim-container {
    margin: 20px;
    height: 100vh;
    flex-direction: row;
    display: flex;
    align-content: center;
    align-items: center;
}

.foto-container {
    display: flex;
    flex: 3;
}

.foto-perfil {
    width: 300px;
    border-radius: 50%;
}

.texto-container {
    height: 300px;
    flex: 9;
    display: flex;
    align-items: center;
    text-align: left;
}